body {
    background: black;
}
.movie_header {
    font-size: 46px;
    color: #fff;
}

.segment {
    width: 100%;
}

.segment form {
    width: 100%;
}

.input_div {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.input_div input {
    width: 65%;
    height: 50px;
    border: none;
    border-radius: 15px;
    font-size: 24px;
    padding-left: 30px;
}

.input_div select {
    width: 20%;
    height: 50px;
    border: none;
    border-radius: 15px;
    font-size: 20px;
    padding: 0 10px;
}

.movie_wrapper {
    margin-top: 70px;
    padding: 0 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    color: #fff;
}

.movie_wrapper .movie_card {
    width: 200px;
    height: 400px;
    margin-bottom: 50px;
    overflow-y: hidden;
}

.movie_wrapper .movie_card:hover {
    width: 220px;
}

.movie_wrapper .movie_card img  {
    width: 100%;
    height: 270px;
}

.movie_wrapper .movie_card p {
    margin-top: 10px;
    text-align: left;
    width: 100%;
}

@media (max-width: 375px) {
    .movie_header {
        font-size: 36px;
        color: #fff;
    }

    .input_div {
        flex-direction: column;
        padding: 0 10px;
    }

    .input_div input {
        width: 100%;
    }
    
    .input_div select {
        width: 40%;
    }

    .movie_wrapper {
        margin-top: 70px;
        padding: 0 20px;
        flex-direction: column;
    }
    
    .movie_wrapper .movie_card {
        width: 100%;
        height: 400px;
        margin-bottom: 50px;
        overflow-y: hidden;
    }
    
    .movie_wrapper .movie_card:hover {
        width: 103%;
    }
    
    .movie_wrapper .movie_card img  {
        width: 100%;
        height: 270px;
    }
    
    
}